import React from 'react'

const Addtodo = (props) => {
    const {todoText, changeText, addTodo} = props;
    return (
        <div className="input-group ">
            <input 
            type="text" 
            className="form-control" 
            placeholder="Enter your todo"
            value={todoText}
            onChange={changeText}
            />
            <div className="input-group-append">
                <button className="btn btn-primary" onClick={addTodo} disabled={todoText==""}>Add Todo</button>
            </div>
        </div>
    )
}

export default Addtodo
