import React from 'react'

const TodoItem = (props) => {
    const {item, changeCheckBox, deleteTodo} = props;
    return (
        <li className="list-group-item ml-3 d-flex justify-content-between" key={item.id}>
            <div>
            <input className="form-check-input" type="radio" checked={item.isComplected} onChange={() => changeCheckBox(item.id)}/>
            <span className={item.isComplected ? 'complectedTodo' : null}>{item.text}</span>
            </div>
            <a href="#" onClick={() => deleteTodo(item.id)}><i className="far fa-trash-alt"></i></a>
        </li>
    )
}

export default TodoItem
