import React, {useState} from 'react'
import { v4 as uuidv4 } from 'uuid';
import Addtodo from './Addtodo';
import TodoItem from './TodoItem';

const Mytodo = () => {
    const [todoText, setTodoText] = useState("");
    const [todo, setTodo] = useState([
        {
            id:1,
            text: 'Website design for client',
            isComplected: false
        },
        {
            id:2,
            text: 'Create design flow for xyz app',
            isComplected: false
        },
        {
            id:3,
            text: 'Play cricket with xyz team',
            isComplected: false
        },
        {
            id:4,
            text: 'Google meet meeting with client',
            isComplected: true
        }
        
    ]);

    const changeText = (event) =>{
        setTodoText(event.target.value);
    }

    const addTodo = () =>{
        const newTodo = {
            id: uuidv4(),
            text: todoText,
            isComplected: false
        }
        setTodo([newTodo, ...todo]);
        setTodoText("");
    }

    const changeCheckBox = (id) =>{
        const updateTodo = todo.map(item =>{
            if(item.id == id){
                item.isComplected = true;
                return item;
            } else{
                return item;
            }
        });
        setTodo(updateTodo);
    }

    const deleteTodo  = (id) =>{
        let updateTodo = todo.filter(item => item.id != id);
        setTodo(updateTodo);
    }

    return (
        <div className="my-todo mt-5">
            <h4 className="text-center p-2 text-primary">TODO APP</h4>
            <div className="card shadow">
                <div className="card-header bg-default">
                    <Addtodo todoText={todoText} changeText={changeText} addTodo={addTodo} todoText={todoText}/>
                </div>
                <ul className="list-group list-group-flush">
                    {
                        todo.map(item => <TodoItem key={item.key} item={item} changeCheckBox={changeCheckBox} deleteTodo={deleteTodo} />)
                    }
                </ul>
            </div>
        </div>
    )
}

export default Mytodo
